const getBotInputState = state => state.botInputState;

const getAgentInputState = state => state.agentState.inputState;

const getAgentState = state => state.agentState;

const getPersistedAgent = state => state.persistedToBrowserStorage.chatState.agentState;

function selectAgentDisplayState(state) {
  const agentState = getAgentState(state);
  const persisted = getPersistedAgent(state);
  if (persisted.isSuspended) {
    return {
      isConnectingOrConnected: false,
      disableInput: false,
      isAgentTyping: false,
      inputPlaceholderKey: null
    };
  }
  const {isReconnecting: isReconnecting, isConnecting: isConnecting, isAgentTyping: isAgentTyping} = agentState;
  const {isConnected: isConnected} = persisted;
  let inputPlaceholderKey;
  if (isConnecting) {
    inputPlaceholderKey = "agent_inputPlaceholderConnecting";
  } else if (isReconnecting) {
    inputPlaceholderKey = "agent_inputPlaceholderReconnecting";
  } else {
    inputPlaceholderKey = null;
  }
  return {
    isAgentTyping: isAgentTyping,
    isConnectingOrConnected: isConnecting || isConnected,
    disableInput: isConnecting || isReconnecting,
    inputPlaceholderKey: inputPlaceholderKey
  };
}

function selectIsInputToAgent(state) {
  return selectAgentDisplayState(state).isConnectingOrConnected;
}

function selectInputState(state) {
  return selectIsInputToAgent(state) ? getAgentInputState(state) : getBotInputState(state);
}

